                viewBox="0 0 27 28"
                version="1.1"
                xmlns="http://www.w3.org/2000/svg"
                xmlnsXlink="http://www.w3.org/1999/xlink"
              >
                <g stroke="none" strokeWidth="1" fill="none" fillRule="evenodd">
                  <g
                    transform="translate(-1676.000000, -38.000000)"
                    fill="#FFFFFF"
                    fillRule="nonzero"
                  >
                    <g transform="translate(1451.000000, 38.000000)">
                      <path d="M244.315208,21.8053097 L243.168224,28 L236.740786,21.8053097 L225,21.8053097 L225,0 L252,0 L252,21.8053097 L244.315208,21.8053097 Z M247.089533,17.4620885 L247.089533,4.46017699 L243.130421,4.46017699 L243.130421,12.302193 L232.468162,4.46017699 L228.280374,4.46017699 L228.280374,17.4620885 L232.468162,17.4620885 L232.468162,9.50957467 L243.130421,17.4620885 L247.089533,17.4620885 Z" />
                    </g>
                  </g>
                </g>
